@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Lexend+Deca&display=swap');

.card {
  width: 840px;
  height: 470px;
  border-radius: 12px;
  display: flex;
  box-shadow: 8px 6px 23px rgba(0, 0, 0, 0.5);
}

.sedans {
  background-color: #bf722a;
  border-radius: 12px 0 0 12px;
}

.suvs {
  background-color: #006970;
}

.luxury {
  background-color: #004241;
  border-radius: 0 12px 12px 0;
}

.sedans,
.suvs,
.luxury {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  color: #fff;
  padding: 50px 50px 0 36px;
}

.sedans img,
.suvs img,
.luxury img {
  width: 70px;
  margin-bottom: 25px;
}

.sedans h1,
.suvs h1,
.luxury h1 {
  font-family: 'Big Shoulders Display', cursive;
  font-size: 35px;
  opacity: 0.9;
  margin-bottom: 50px;
}

.sedans p,
.suvs p,
.luxury p {
  font-size: 14px;
  font-family: 'Lexend Deca', sans-serif;
  font-weight: 100;
  line-height: 1.6rem;
  opacity: 0.8;
  margin-bottom: 45px;
}

.sedans button,
.suvs button,
.luxury button {
  cursor: pointer;
  font-family: 'Lexend Deca', sans-serif;
  font-size: 16px;
  width: 150px;
  padding: 13px;
  border: 2px solid #fff;
  border-radius: 50px;
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}
.sedans button {
  color: #bf722a;
}

.suvs button {
  color: #006970;
}

.luxury button {
  color: #004241;
}

.sedans button:hover,
.suvs button:hover,
.luxury button:hover {
  color: #fff;
}

.sedans button:hover {
  background-color: #bf722a;
}

.suvs button:hover {
  background-color: #006970;
}

.luxury button:hover {
  background-color: #004241;
}

footer {
  align-self: flex-end;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3d52a3;
}

@media (max-width: 991px) {
  .card {
    width: 550px;
    height: 420px;
  }

  .sedans,
  .suvs,
  .luxury {
    padding: 30px 15px 0 15px;
    position: relative;
  }

  .sedans img,
  .suvs img,
  .luxury img {
    margin-inline: 13px;
    margin-block-end: 30px;
  }

  .sedans h1,
  .suvs h1,
  .luxury h1 {
    padding-inline: 13px;
    margin-block-end: 30px;
  }

  .sedans p,
  .suvs p,
  .luxury p {
    padding-inline: 13px;
    line-height: 1.1rem;
    font-size: 13px;
    margin-bottom: 20px;
  }

  .sedans button,
  .suvs button,
  .luxury button {
    width: 100px;
    font-size: 13px;
    position: absolute;
    left: 20px;
    bottom: 30px;
    padding: 10px;
  }
}

@media (max-width: 575px) {
  .card {
    width: 330px;
    height: max-content;
    flex-direction: column;
    margin-bottom: 100px;
  }

  .sedans {
    border-radius: 12px 12px 0 0;
  }
  .luxury {
    border-radius: 0 0 12px 12px;
  }

  .sedans img,
  .suvs img,
  .luxury img {
    margin-inline: 25px;
  }

  .sedans h1,
  .suvs h1,
  .luxury h1 {
    padding-inline: 25px;
    margin-block-end: 30px;
  }

  .sedans p,
  .suvs p,
  .luxury p {
    padding-inline: 25px;
    margin-block-end: 110px;
  }

  .sedans button,
  .suvs button,
  .luxury button {
    left: 40px;
  }
}
